@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* GENERAL */
.fw-600 {
  font-weight: 600;
}
.mt-60 {
  margin-top: 60px;
}

/* LOGO */
.logo img {
  width: 70%;
}

/* HOME PAGE */
.home .welcome {
  color: #223E5C;
  font-weight: 600;
  font-size: 36px;
}
.home .welcome span {
  color: #28A5DD;
}

/* FOOTER */
footer .font-weight-semibold {
  color: #28A5DD;
}

/* NAVBAR */
.ant-menu-item a, .ant-menu-item>.ant-badge a {
  color: #223E5C;
}
.ant-menu-item-selected, .ant-menu-item-selected a, .ant-menu-item-selected a:hover, .ant-menu-submenu:hover > .ant-menu-submenu-title > .ant-menu-submenu-arrow, .ant-menu-submenu:hover > .ant-menu-submenu-title > .ant-menu-submenu-expand-icon {
  color: #28A5DD !important;
}
.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background-color: rgba(34, 62, 92, 0.05);
}
.ant-menu-item .ant-menu-item-selected {
  border-color: #223E5C !important;
}

/* BREADCRUMB */
.ant-breadcrumb,
.ant-breadcrumb a {
  color: #28A5DD;
  font-size: 12px;
  transition: 0.3s;
  margin-bottom: 9px;
}
.ant-breadcrumb a:hover {
  color: #223E5C;
}

/* TABLE */
.table .ant-spin-container {
  padding: 0;
}

.ant-table-content thead tr th {
  background-color: #223E5C;
  color: #fff;
}
.ant-table-content thead tr th::before {
  background: unset !important;
}
.ant-table-container table > thead > tr:first-child th:first-child {
  border-top-left-radius: unset !important;
}
.ant-table-container table > thead > tr:first-child th:last-child {
  border-top-right-radius: unset !important;
}

.ant-table-content td {
  color: #223E5C;
}

.home-table-action {
  position: relative;
}

.home-table-btn,
.home-table-btn:hover {
  height: 28px;
  background: #f1e9d5;
  color: #ba902e;
  border-radius: 5px;
  font-size: 12px;
  line-height: 1px;
}

.home-table-btn-review {
  position: absolute;
  top: 0%;
  width: 10px;
  height: 10px;
  background-color: #d0342c;
  border-radius: 50%;
}

.action-icons svg {
  visibility: hidden;
}
.action-icons svg.red {
  visibility: visible;
}

.ant-table-tbody tr:hover .action-icons svg {
  visibility: visible;
}

/* Setting small fontsize for large tables */
.large-table .ant-table-content tr {
  font-size: 12px;
}
.large-table .ant-table-tbody tr .action-icons svg {
  width: 20px;
  height: 20px;
}

@media (min-width: 1400px) {
  .large-table .ant-table-content tr {
    font-size: 14px;
  }
  .large-table .ant-table-tbody tr:hover .action-icons svg {
    width: 24px;
    height: 24px;
  }
}

/* Changing font size for large tables */
.small-table .ant-table-content tr {
  font-size: 16px;
}

/* Table Selection */
.ant-table-tbody > tr.ant-table-row-selected > td,
.ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #fafafa;
  border-color: rgba(0, 0, 0, 0.03);
}

.ant-checkbox-checked .ant-checkbox-inner {
  background-color: #eee;
  border-color: #223E5C;
}

.ant-checkbox-input:focus + .ant-checkbox-inner {
  border-color: #223E5C;
  background-color: #eee;
}

.ant-checkbox:hover .ant-checkbox-inner {
  border-color: #223E5C;
}

.ant-checkbox-wrapper:hover .ant-checkbox-inner {
  border-color: #223E5C;
}

.ant-checkbox-inner {
  border-color: #223E5C;
  background-color: #eee;
}

.ant-checkbox-checked .ant-checkbox-inner::after {
  border-color: #223E5C;
}

/* CARDS */
.ant-card {
  border-radius: 5px;
}
.ant-card-head-wrapper {
  padding-top: 18px;
  padding-bottom: 18px;
  align-items: center;
}
.ant-card-head {
  border-bottom: 0;
}
.ant-card-head-title {
  font-size: 20px;
  font-weight: 500;
  color: #223E5C;
  padding-top: 0 !important;
}
.ant-card-body {
  padding: 0;
}
.ant-card-extra {
  padding-top: 0;
}
/* Create BUTTON */
.create-btn {
  padding: 10px 16px;
  color: #223E5C;
  font-weight: 600;
  border: 1px solid #223E5C;
  border-radius: 8px;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: 0.2s ease-out;
}
.create-btn svg {
  font-size: 28px;
  margin-right: 13px;
}
.create-btn:hover {
  background: #223E5C;
  color: #fff;
}

.display-btn svg,
.edit-btn svg,
.delete-btn svg {
  font-size: 22px;
  cursor: pointer;
  transition: 0.3s ease-out;
}
.edit-btn {
  margin: 0 10px;
}
.delete-btn:hover svg {
  color: #D0342C;
}
.display-btn:hover svg {
  color: #2A9F20;
}

.submit-btn {
  width: 100%;
  background: #28A5DD;
  border-color: #28A5DD;
  color: #fff;
  border-radius: 5px;
}
.submit-btn:hover {
  background: #0f94d1;
}

/* FORM */
.ant-form-item-label {
  margin-bottom: 0;
  padding: 0 !important;
}
.ant-form-item-label label {
  color: #223E5C;
  font-weight: 700;
}
.ant-input,
.ant-input-password {
  border: unset;
  border-bottom: 2px solid #223E5C;
  border-radius: unset;
  background: transparent;
  color: rgba(0, 0, 0, 0.5);
  padding-top: 0 !important;
}
.ant-input:focus,
.ant-input-affix-wrapper:focus,
.ant-input-password:focus,
.ant-input-affix-wrapper-focused {
  box-shadow: unset !important;
  outline: unset !important;
  border-color: #28A5DD;
}

.ant-select-selector {
  border: none !important;
  background: transparent !important;
}
.ant-select-selector:focus,
.ant-select:focus,
.ant-select-focused .ant-select-selector {
  box-shadow: unset !important;
  outline: none;
}
.ant-select {
  border-bottom: 2px solid #223E5C !important;
}
.ant-select-arrow svg {
  color: #223E5C;
}

/* DELETE POP-UP */
.ant-modal.delete-popup {
  width: 404px !important;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  overflow: hidden;
}

.ant-modal.delete-popup .ant-modal-body {
  padding: 24px;
}
.ant-modal.delete-popup svg {
  color: #FF2F22;
  font-size: 90px;
  margin-bottom: 10px;
}
.ant-modal.delete-popup h3 {
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 16px;
  color: #223E5C;
}

.ant-modal.delete-popup button,
.ant-modal.delete-popup button:hover {
  background: #FF2F22;
  border-radius: 6px;
  width: 100%;
  padding: 13px 0;
  border: 1px solid #FF2F22;
  border-radius: 7.34375px;
  font-weight: 600;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
}

/* STATISTICS */
.statistics .card-lg {
  background: #FFFFFF;
  border-radius: 6px;
  padding: 16px;
}
.statistics .card-lg .header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
}
.statistics .card-lg .header svg {
  margin-right: 8px;
  width: 40px;
  height: 35px;
}
.statistics .card-lg .header h4 {
  color: #223E5C;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 0;
}

.statistics .item {
  color: #216DA4;
  padding: 8px;
  text-align: center;
}
.statistics .item .desc {
  margin-bottom: 12px;
  font-size: 14px;
}
.statistics .item .number {
  color: #216DA4;
  font-size: 22px;
  font-weight: 600;
}
.statistics .item.danger,
.statistics .item.danger .number {
  color: #EB001B;
}

.statistics .ant-card-bordered {
  border: none;
}
.statistics .ant-card-body {
  padding: 16px 24px;
}
.statistics .ant-card-body h4 {
  color: #223E5C;
  font-size: 24px;
  font-weight: 600 !important;
  margin-bottom: 0;
}

.statistics .graph-info {
  color: #718EBF;
  font-size: 28px;
  padding-left: 18px;
}
.statistics .graph-info span:first-child {
  margin-right: 20px;
}

/* LOGIN PAGE */
.login .img-fluid {
  width: 150px;
  margin-bottom: 36px;
}

/* HOME BREADCRUMB */
.home .ant-breadcrumb {
  display: none;
}
body {
  font-family: 'Poppins', sans-serif;
}

.loading.cover-content {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.loading.cover-page {
  position: fixed;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loading .ant-spin {
  color: #3e79f7;
}


